.main-nav {
  position: relative;
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  width: 320px;
  min-height: 198px;
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 440px;
    min-height: 60px;
  }
}
@media (min-width: 1300px) {
  .main-nav__list {
    width: 480px;
    min-height: 111px;
  }
}
.main-nav__toggle {
  position: absolute;
  top: -53px;
  right: 8px;
  font-size: 0;
  width: 47px;
  height: 37px;
  background-color: transparent;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 11px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 18px;
  left: 11px;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .main-nav--closed .site-list {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .site-list {
    display: block;
  }
}
